import Header from './Header';
import Main from './Main';
import LockScreen from './LockScreen';
import '../stylesheets/App.css';
import { useState } from 'react';

function App() {
  const [loggedIn, setLoggedIn] = useState(true);

  function login() {
    setLoggedIn(true);
  }

  return <>
    {loggedIn ?
      <>
        <Header />
        <Main />
      </>
    :
      <>
        <LockScreen login={login} />
      </>
    }
  </>
    
}

export default App;
